.add-note-btn{
    margin: 2% 40%;
    width: 20%;
    justify-content: center;
    text-align: center;
}

.close{
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
}

.twitter-picker {
    margin-top: 10px;
}

div.DraftEditor-root {
    text-align: left;
    width: 95%;
    margin: 0%;
    height: 100px !important;
    padding: 2% !important;
    border: 1px solid black;
}

.title-div {
    width: 100%;
    margin-bottom: 10px;
}

.editor-buttons {
    float: left;
    padding: 0.5%;
    margin-bottom: 10px;
    margin-left: 25px;
    border-radius: 10%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}

.editor-buttons > button {
    height: 100%;
    border-style: none;
    font-size: 18px;
    background-color: #ffffff;
}

.toolbar-class {
    width: 98.5%;
    padding: 6px 5px 0;
    border-radius: 2px;
    border: 1px solid #F1F1F1;
    display: flex;
    justify-content: flex-start;
    background: white;
    flex-wrap: wrap;
    font-size: 15px;
    margin-bottom: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.rdw-inline-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    margin-top: 6px;
    height: 20px;
}

.rdw-option-active {
    box-shadow: 1px 1px 0px #BFBDBD inset;
}

.rdw-option-wrapper {
    border: 1px solid #F1F1F1;
    padding: 5px;
    min-width: 25px;
    border-radius: 2px;
    margin: 0 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: white;
    text-transform: capitalize;
}

.rdw-block-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
}

.rdw-dropdown-wrapper {
    height: 30px;
    background: white;
    cursor: pointer;
    border: 1px solid #F1F1F1;
    border-radius: 2px;
    margin: 0 3px;
    text-transform: capitalize;
    background: white;
}

.rdw-dropdown-carettoopen {
    height: 0px;
    width: 0px;
    position: absolute;
    top: 35%;
    right: 10%;
    border-top: 6px solid black;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
}

.rdw-dropdown-carettoclose {
    height: 0px;
    width: 0px;
    position: absolute;
    top: 35%;
    right: 10%;
    border-bottom: 6px solid black;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
}

.rdw-block-dropdown {
    width: 110px;
}

.rdw-dropdown-optionwrapper {
    z-index: 100;
    position: relative;
    border: 1px solid #F1F1F1;
    width: 98%;
    background: white;
    border-radius: 2px;
    margin: 0;
    padding: 0;
    max-height: 250px;
    overflow-y: scroll;
}

.rdw-dropdownoption-active {
    background: #f5f5f5;
}

.rdw-dropdownoption-default {
    min-height: 25px;
    display: flex;
    align-items: center;
    padding: 0 5px;
}

.rdw-dropdown-selectedtext {
    display: flex;
    position: relative;
    height: 100%;
    align-items: center;
    padding: 0 5px;
}

.rdw-fontsize-dropdown {
    min-width: 40px;
}

.rdw-fontfamily-dropdown {
    width: 115px;
}

.rdw-fontsize-wrapper, .rdw-fontfamily-wrapper, .rdw-list-wrapper,
.rdw-text-align-wrapper, .rdw-text-align-wrapper, .rdw-history-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
}

.rdw-colorpicker-wrapper, .rdw-emoji-wrapper,
.rdw-image-wrapper, .rdw-remove-wrapper, .rdw-link-wrapper,
.rdw-embedded-wrapper  {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    position: relative;
}


.tagContainer{
    border: 1px solid #ddd;
    padding: 5px;
    border-radius: 5px;
}

.tagItems{
    display: inline-block;
    padding: 2px;
    border: 1px solid black;
    font-family: Helvetica, sans-serif;
    border-radius: 5px;
    margin-right: 5px;
    cursor: pointer;
}

.rdw-emoji-modal {
    overflow: auto;
    position: absolute;
    top: 35px;
    left: 5px;
    display: flex;
    flex-wrap: wrap;
    width: 235px;
    height: 180px;
    border: 1px solid #F1F1F1;
    padding: 15px;
    border-radius: 2px;
    z-index: 100;
    background: white;
    box-shadow: 3px 3px 5px #BFBDBD;
}


.rdw-image-modal {
    position: absolute;
    top: 35px;
    left: 5px;
    display: flex;
    flex-direction: column;
    width: 235px;
    border: 1px solid #F1F1F1;
    padding: 15px;
    border-radius: 2px;
    z-index: 100;
    background: white;
    box-shadow: 3px 3px 5px #BFBDBD;
}

.rdw-colorpicker-modal {
    position: absolute;
    top: 35px;
    left: 5px;
    display: flex;
    flex-direction: column;
    width: 175px;
    height: 175px;
    border: 1px solid #F1F1F1;
    padding: 15px;
    border-radius: 2px;
    z-index: 100;
    background: white;
    box-shadow: 3px 3px 5px #BFBDBD;
}


.rdw-link-modal {
    position: absolute;
    top: 35px;
    left: 5px;
    display: flex;
    flex-direction: column;
    width: 235px;
    height: 205px;
    border: 1px solid #F1F1F1;
    padding: 15px;
    border-radius: 2px;
    z-index: 100;
    background: white;
    box-shadow: 3px 3px 5px #BFBDBD;
}

.rdw-colorpicker-modal-header {
    display: flex;
    padding-bottom: 5px;
}

.rdw-colorpicker-modal-options {
    margin: 5px auto;
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    overflow: scroll;
}

.rdw-colorpicker-cube {
    width: 22px;
    height: 22px;
    border: 1px solid #F1F1F1;
}


