.bm-item {
  display: inline-block;
  text-align: left;
  text-decoration: none;
  margin-top: 30px;
  margin-bottom: 30px;
  color: #137ECE;
  transition: color 1.9s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: fixed;
  width: 24px;
  height: 22px;
  left: 1.5%;
  top: 2.5%;
}

.bm-burger-bars {
  background: #137ECE;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #137ECE;
}

.bm-menu {
  background: #FFFFFF;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  overflow: hidden;
}

.bm-morph-shape {
  fill: #137ECE;
